import React from 'react';
import '../style/main.scss'
import { Link, Outlet, Route, Routes } from "react-router-dom";
import MainList from "../shop/MainList";
import MainVisual from "./MainVisual";
import ReactDOM from 'react-dom';
import ReactFullpage from '@fullpage/react-fullpage';
import ListAll from '../shop/ListAll';
import Footer from './Footer';




const Main = ({ shopData, sw }) => {
    const anchors = [];
    const TabData = ['liquid', 'palette', 'concealer']

    return (
        <ReactFullpage
            //fullpage options
            licenseKey={'YOUR_KEY_HERE'}
            scrollingSpeed={1000} /* Options here */
            scrollOverflow={true}

            navigation={true}
            navigationPosition="right"
            navigationTooltips={anchors}

            render={({ state, fullpageApi }) => {
                return (
                    <ReactFullpage.Wrapper>
                        <div className="section">
                            <section className='main main01'>
                                <h2>No.1  </h2>
                                <p>11    <br />
                                       </p>
                                <a href=""> </a>
                            </section>
                        </div>
                        <div className='section'>
                            <section className='main main02'>
                                <h2> </h2>
                                <p> <br />
                                     </p>
                                <a href=""> </a>
                            </section>
                        </div>
                        <div className='section'>
                            <section className='main main03'>
                                <h2> </h2>
                                <p>    !<br />
                                       </p>
                                <a href=""> </a>
                            </section>
                        </div>
                        <div className="section">
                            <section className="mainTab">
                                <h2> </h2>
                                <ul className="menu">
                                    {
                                        TabData.map((it, idx) => { // idx = 0,1,2
                                            return <Link to={`/tab/${it}`}>{it}</Link>
                                        })
                                    }
                                </ul>
                                <div className="con">
                                    <Outlet />
                                </div>
                            </section>
                        </div>
                        <div className='section fp-auto-height'>
                            <Footer />
                        </div>
                    </ReactFullpage.Wrapper>
                );
            }}
        />
    )
}


export default Main;